export default {
  state: {
    sellerId: null,
    orderItems: [],
    addressList: [],
    comment: ""
  },
  actions: {
    fetchAddressList(context) {
      setTimeout(() => {
        context.commit("setAddressList", addressList)
      }, 500)
    },
    postOrder(context, order) {
      console.log(order)
    }
  },
  mutations: {
    setAddressList(state, addressList) {
      state.addressList = addressList;
    }
  },
  getters: {
    getAddressList(state) {
      return state.addressList;
    }
  }
}



